import React, { Component } from "react";
import { Link, RichText, Date } from "prismic-reactjs";
import { bindActionCreators } from "redux";
import { fetchAllPrismic } from "../actions/prismicAction";
import { connect } from "react-redux";

class PostsList extends Component {
  componentDidMount() {
    this.props.fetchAllPrismic();
  }

  renderPosts() {
    console.log("le data", this.props.data);

    this.props.data.length >= 1 ? (
      this.props.data.map(posts => {
        if (posts.type === "post") {
          posts.map(post => {
            return (
              <ul key={posts.id}>
                <li>{RichText.asText(post.title)}</li>
                <li>{RichText.asText(post.description)}</li>
                <img alt="cover" src={post.image.url} />
              </ul>
            );
          });
        }
      })
    ) : (
      <p>loading...</p>
    );
  }

  render() {
    // console.log("fetch", this.props.posts);
    return (
      <div>
        <h3>header</h3>
        {this.renderPosts()}
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    data: state.data
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({ fetchAllPrismic }, dispatch);
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PostsList);

